import {
  Box,
  Flex,
  Avatar,
  HStack,
  Link,
  IconButton,
  Button,
  Menu,
  MenuButton,
  useDisclosure,
  useColorModeValue,
  Stack,
} from '@chakra-ui/react';
import { HamburgerIcon, CloseIcon } from '@chakra-ui/icons';
//import ColorModeToggle from './ColorModeToggle';
import Home from "../Pages/Home";
import About from '../Pages/About';
import Footer from '../Pages/Skills';
import Skills from '../Pages/skills/Skills'
import TechStack from '../Pages/Tech Stack/TechStack'
// import Skills from '../Pages/Skills';
 
const Links = ['Home', 'About', 'Skills', 'Projects', 'Contact',"Resume"];

const NavLink = ({children}) => (
  <Link
    fontSize={'15px'}
    fontWeight={600}
    px={3}
    py={1}
 
    rounded={'md'}
    _hover={{
      textDecoration: 'none',
      bg: useColorModeValue('red.200', 'red.700'),
    }}
    color='white'


    href={'#'}>
    {children}
  </Link>
);

export default function Navbar() {
  const { isOpen, onOpen, onClose } = useDisclosure();

  return (
    <Box >
      <Box  bg="teal.600" px={4} position={'fixed'} overflow={'hidden'} top="0" width={'100%'}>
        <Flex h={20} alignItems={'center'} justifyContent={'space-between'}>
          <IconButton
            size={'md'}
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            aria-label={'Open Menu'}
            display={{ md: 'none' }}
            onClick={isOpen ? onClose : onOpen}
          />
          <HStack spacing={8} color="white"  alignItems={'center'}>
            <h1 fontSize="40px"  color='white'>𝒩𝒾𝓉𝒾𝓃 𝒦𝑜𝓃𝒹𝒽𝒶𝓇𝒾</h1>
          </HStack>
          <Flex alignItems={'center'}>
            <Menu>
              <MenuButton
                as={Button}
                rounded={'full'}
                variant={'link'}
                cursor={'pointer'}
                minW={0}>
              </MenuButton>
              <HStack
              as={'nav'}
              spacing={4}
              display={{ base: 'none', md: 'flex' }}>
              {Links.map((link) => (
                <NavLink key={link}>{link}</NavLink>
              ))}
            </HStack>
            </Menu>
         {/* <ColorModeToggle /> */}
          </Flex>
        </Flex>

        {isOpen ? (
          <Box pb={4} display={{ md: 'none' }}>
            <Stack as={'nav'} spacing={4}>
              {Links.map((link) => (
                <NavLink key={link}>{link}</NavLink>
              ))}
            </Stack>
          </Box>
        ) : null}
      </Box>

      <Box p={8}  >
        <Home  />
        <About />
       <TechStack/>
       {/* <Skills/> */}
      </Box>
    </Box>
  );
}